.sale_page {

  header {
    &.header {
      z-index: 3;
    }
  }

  .container-sales{
    width: 100%;
    padding: 0 24px;
    margin: 0 auto;
    @media screen and (min-width: 320px) {
      max-width: 320px;
    }
    @media screen and (min-width: 768px) {
      max-width: 768px;
      padding: 0 32px;
    }

    @media screen and (min-width: 1200px) {
      max-width: 1200px;
    }
  }
  .container-sales--flex-desk{
    @media screen and (min-width: 1200px) {
      display: flex;
    }
  }
  .container-sales--flex-tab{
    @media screen and (min-width: 768px) {
      display: flex;
      padding: 0 32px;
    }
  }

}
